@import "src/scss/vars";
.releases{
  .mainTitle {
    margin-bottom: 20px;
  }
  .releasesInner{
    display: inline-grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 25px;
  }
}

.movieItem{
  position: relative;
  .voteAverage {
    position: absolute;
    top: 0;
    transform: translate(50%, 50%);
    background-color: red;
    border-radius: 10px;
    padding: 10px;
  }
  .poster{
    display: block;
    width: 250px;
    img {
      width: 100%;
    }
  }
}